<template><div class="markdown-body"><h1>网格列表</h1>
<p><code v-pre="">mu-grid-list</code> 网格列表是一种标准列表视图的可选组件。网格列表与应用于布局和其他可视视图中的网格有着明显的区别。</p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;mu-flex justify-content=\&quot;center\&quot;&gt;\n    &lt;mu-paper :z-depth=\&quot;1\&quot;&gt;\n      &lt;mu-appbar color=\&quot;primary\&quot;&gt;\n        &lt;mu-button icon slot=\&quot;left\&quot;&gt;\n          &lt;mu-icon value=\&quot;menu\&quot;&gt;&lt;/mu-icon&gt;\n        &lt;/mu-button&gt;\n        Photo\n      &lt;/mu-appbar&gt;\n      &lt;mu-grid-list class=\&quot;gridlist-demo\&quot;&gt;\n        &lt;mu-sub-header&gt;December&lt;/mu-sub-header&gt;\n        &lt;mu-grid-tile v-for=\&quot;tile, index in list\&quot; :key=\&quot;index\&quot;&gt;\n          &lt;img :src=\&quot;tile.image\&quot;&gt;\n          &lt;span slot=\&quot;title\&quot;&gt;{{tile.title}}&lt;/span&gt;\n          &lt;span slot=\&quot;subTitle\&quot;&gt;by &lt;b&gt;{{tile.author}}&lt;/b&gt;&lt;/span&gt;\n          &lt;mu-button slot=\&quot;action\&quot; icon&gt;\n            &lt;mu-icon value=\&quot;star_border\&quot;&gt;&lt;/mu-icon&gt;\n          &lt;/mu-button&gt;\n        &lt;/mu-grid-tile&gt;\n      &lt;/mu-grid-list&gt;\n    &lt;/mu-paper&gt;\n  &lt;/mu-flex&gt;\n&lt;/mu-container&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\nimport breakfast from '../../assets/images/breakfast.jpg';\nimport burger from '../../assets/images/burger.jpg';\nimport camera from '../../assets/images/camera.jpg';\nimport hats from '../../assets/images/hats.jpg';\nimport honey from '../../assets/images/honey.jpg';\nimport morning from '../../assets/images/morning.jpg';\nimport vegetables from '../../assets/images/vegetables.jpg';\nimport waterPlant from '../../assets/images/water-plant.jpg';\nexport default {\n  data () {\n    return {\n      list: [{\n        image: breakfast,\n        title: 'Breakfast',\n        author: 'Myron'\n      }, {\n        image: burger,\n        title: 'Burger',\n        author: 'Linyu'\n      }, {\n        image: camera,\n        title: 'Camera',\n        author: 'ruolin'\n      }, {\n        image: hats,\n        title: 'Hats',\n        author: 'kakali'\n      }, {\n        image: honey,\n        title: 'Honey',\n        author: 'yuyang'\n      }, {\n        image: morning,\n        title: 'Morning',\n        author: 'mokayi'\n      }, {\n        image: vegetables,\n        title: 'Vegetables',\n        author: 'NUyyyyyyy'\n      }, {\n        image: waterPlant,\n        title: 'water',\n        author: 'TDDyyyyyyy'\n      }]\n    }\n  }\n}\n&quot;,&quot;style&quot;:&quot;\n.gridlist-demo{\n  width: 360px;\n  height: 450px;\n  overflow-y: auto;\n}\n&quot;}">
                    <div class="source" slot="source"><mu-container>
  <mu-flex justify-content="center">
    <mu-paper :z-depth="1">
      <mu-appbar color="primary">
        <mu-button icon="" slot="left">
          <mu-icon value="menu"></mu-icon>
        </mu-button>
        Photo
      </mu-appbar>
      <mu-grid-list class="gridlist-demo">
        <mu-sub-header>December</mu-sub-header>
        <mu-grid-tile v-for="tile, index in list" :key="index">
          <img :src="tile.image">
          <span slot="title">{{tile.title}}</span>
          <span slot="subTitle">by <b>{{tile.author}}</b></span>
          <mu-button slot="action" icon="">
            <mu-icon value="star_border"></mu-icon>
          </mu-button>
        </mu-grid-tile>
      </mu-grid-list>
    </mu-paper>
  </mu-flex>
</mu-container>


</div>
                    
