            <span class="hljs-tag">&lt;<span class="hljs-name">mu-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"star_border"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-icon</span>&gt;</span>
          <span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">mu-grid-tile</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-grid-list</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-paper</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-flex</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-container</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">import</span> breakfast <span class="hljs-keyword">from</span> <span class="hljs-string">'../../assets/images/breakfast.jpg'</span>;
<span class="hljs-keyword">import</span> burger <span class="hljs-keyword">from</span> <span class="hljs-string">'../../assets/images/burger.jpg'</span>;
<span class="hljs-keyword">import</span> camera <span class="hljs-keyword">from</span> <span class="hljs-string">'../../assets/images/camera.jpg'</span>;
<span class="hljs-keyword">import</span> hats <span class="hljs-keyword">from</span> <span class="hljs-string">'../../assets/images/hats.jpg'</span>;
<span class="hljs-keyword">import</span> honey <span class="hljs-keyword">from</span> <span class="hljs-string">'../../assets/images/honey.jpg'</span>;
<span class="hljs-keyword">import</span> morning <span class="hljs-keyword">from</span> <span class="hljs-string">'../../assets/images/morning.jpg'</span>;
<span class="hljs-keyword">import</span> vegetables <span class="hljs-keyword">from</span> <span class="hljs-string">'../../assets/images/vegetables.jpg'</span>;
<span class="hljs-keyword">import</span> waterPlant <span class="hljs-keyword">from</span> <span class="hljs-string">'../../assets/images/water-plant.jpg'</span>;
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">list</span>: [{
        <span class="hljs-attr">image</span>: breakfast,
        <span class="hljs-attr">title</span>: <span class="hljs-string">'Breakfast'</span>,
        <span class="hljs-attr">author</span>: <span class="hljs-string">'Myron'</span>
      }, {
        <span class="hljs-attr">image</span>: burger,
        <span class="hljs-attr">title</span>: <span class="hljs-string">'Burger'</span>,
