          <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"action"</span> <span class="hljs-attr">icon</span>&gt;</span>
            <span class="hljs-tag">&lt;<span class="hljs-name">mu-icon</span> <span class="hljs-attr">value</span>=<span class="hljs-string">"star_border"</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">mu-icon</span>&gt;</span>
          <span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
        <span class="hljs-tag">&lt;/<span class="hljs-name">mu-grid-tile</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-grid-list</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-paper</span>&gt;</span>
