<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">import</span> breakfast <span class="hljs-keyword">from</span> <span class="hljs-string">'../../assets/images/breakfast.jpg'</span>;
<span class="hljs-keyword">import</span> burger <span class="hljs-keyword">from</span> <span class="hljs-string">'../../assets/images/burger.jpg'</span>;
<span class="hljs-keyword">import</span> camera <span class="hljs-keyword">from</span> <span class="hljs-string">'../../assets/images/camera.jpg'</span>;
