  <span class="hljs-tag">&lt;<span class="hljs-name">mu-grid-tile</span> <span class="hljs-attr">v-for</span>=<span class="hljs-string">"tile, index in list"</span> <span class="hljs-attr">:key</span>=<span class="hljs-string">"index"</span>&gt;</span>
