<h2 id="grid-list-props"><a class="header-anchor" href="#grid-list-props" aria-hidden="true">¶</a> Grid List Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>cell-height</td>
<td>网格的高度</td>
<td>Number</td>
<td>180</td>
<td></td>
</tr>
<tr>
<td>cols</td>
<td>每一行的列数</td>
<td>Number</td>
<td>2</td>
<td></td>
</tr>
<tr>
<td>padding</td>
<td>每个网格的 <code v-pre="">padding</code></td>
<td>Number</td>
<td>4</td>
<td></td>
</tr>
</tbody>
</table>
<h2 id="grid-tile-props"><a class="header-anchor" href="#grid-tile-props" aria-hidden="true">¶</a> Grid Tile Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>cols</td>
<td>占的列数, 会根据此参数计算宽度</td>
<td>Number</td>
<td>—</td>
<td>1</td>
</tr>
<tr>
<td>rows</td>
<td>占的行数, 会根据此参数计算高度</td>
<td>Number</td>
<td>—</td>
<td>1</td>
</tr>
<tr>
<td>title</td>
<td>标题文字，也可以通过 <code v-pre="">slot=&quot;title&quot;</code> 来设置</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>sub-title</td>
<td>子标题文字，也可以通过 <code v-pre="">slot=&quot;subTitle&quot;</code> 来设置</td>
<td>String</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>title-position</td>
<td>标题栏的位置</td>
<td>String</td>
<td>top/bottom</td>
<td>bottom</td>
</tr>
<tr>
<td>action-position</td>
<td>动作按钮的位置</td>
<td>String</td>
<td>left/right</td>
<td>right</td>
</tr>
</tbody>
</table>
<h2 id="grid-tile-slots"><a class="header-anchor" href="#grid-tile-slots" aria-hidden="true">¶</a> Grid Tile Slots</h2>
<table>
<thead>
<tr>
<th>名称</th>
<th>介绍</th>
</tr>
</thead>
<tbody>
<tr>
<td>title</td>
<td>标题文字插槽</td>
</tr>
<tr>
<td>subTitle</td>
<td>子标题文字插槽</td>
</tr>
<tr>
<td>action</td>
<td>动作按钮插槽</td>
</tr>
<tr>
<td>default</td>
<td>用于放置图片等元素</td>
</tr>
</tbody>
</table>


</div class="markdown-body"></template>
<style>
.gridlist-demo{
  width: 360px;
  height: 450px;
  overflow-y: auto;
}
.gridlist-inline-demo{
  display: flex;
  flex-wrap: nowrap;
  overflow-x: auto;
}
</style>
<script>
import breakfast from '../../assets/images/breakfast.jpg';
import burger from '../../assets/images/burger.jpg';
import camera from '../../assets/images/camera.jpg';
import hats from '../../assets/images/hats.jpg';
import honey from '../../assets/images/honey.jpg';
import morning from '../../assets/images/morning.jpg';
import vegetables from '../../assets/images/vegetables.jpg';
import waterPlant from '../../assets/images/water-plant.jpg';
export default {
  data () {
    return {
      list: [{
        image: breakfast,
        title: 'Breakfast',
        author: 'Myron'
      }, {
        image: burger,
        title: 'Burger',
        author: 'Linyu'
      }, {
        image: camera,
        title: 'Camera',
        author: 'ruolin'
      }, {
        image: hats,
        title: 'Hats',
        author: 'kakali'
      }, {
        image: honey,
        title: 'Honey',
        author: 'yuyang'
      }, {
        image: morning,
        title: 'Morning',
        author: 'mokayi'
      }, {
        image: vegetables,
        title: 'Vegetables',
        author: 'NUyyyyyyy'
      }, {
        image: waterPlant,
        title: 'water',
        author: 'TDDyyyyyyy'
      }],
      list1: [{
          image: breakfast,
          title: 'Breakfast',
          author: 'Myron',
          featured: true
        }, {
          image: burger,
          title: 'Burger',
          author: 'Linyu'
        }, {
          image: camera,
          title: 'Camera',
          author: 'ruolin'
        }, {
          image: hats,
          title: 'Hats',
          author: 'kakali'
        }, {
          image: honey,
          title: 'Honey',
          author: 'yuyang'
        }, {
          image: morning,
          title: 'Morning',
          author: 'mokayi',
          featured: true
        }, {
          image: vegetables,
          title: 'Vegetables',
          author: 'NUyyyyyyy'
        }, {
          image: waterPlant,
          title: 'water',
          author: 'TDDyyyyyyy'
        }]
    }
  }
}
</script>


